import HalstonDriver from './_base';

const BASE_URL = 'https://www.heidiklumintimates.com';

const Driver = class Driver extends HalstonDriver {
	constructor() {
		super();

		this.CART_API_URL = `${BASE_URL}/api/v3/carts`;
	}
};

export default {
	name: 'Heidi Klum Intimates',
	domain: 'heidiklumintimates.com',
	Driver,
	isCartEmpty() {
		const emptyElement = document.querySelector('.cart-page-empty');

		return emptyElement && emptyElement.offsetParent;
	},
	isOnCartUrl(href) {
		return href.startsWith(`${BASE_URL}/cart`);
	},
};
