import bus from '../bus';
import { log, format } from '../../utils';
import { backgroundMessaging, handleDriverError } from '../lib';

const { toFloat } = format;

export default class BaseDriver {
	/**
	 * @function watchForRestoreUserData - set up a listener to check user data restorement
	 * @emits bus#RestoreUserData - the event that indicates to the app	that user data could be restored
	 */
	watchForRestoreUserData(time = 500) {
		let pauseInterval = false;
		const interval = setInterval(async () => {
			if (!pauseInterval) {
				try {
					// Pause interval to avoid triggering multiple times `checkCondition`
					// before the response will be received
					pauseInterval = true;
					const currentState = await this.checkRestoreDataCondition();
					pauseInterval = false;
					if (currentState) {
						clearInterval(interval);
						bus.$emit('restore-user-data');
					}
				} catch (e) {
					handleDriverError(e, { source: 'checkRestoreDataCondition' });
					clearInterval(interval);
				}
			}
		}, time);

		return interval;
	}

	checkRestoreDataCondition() {
		return false;
	}

	/**
	 * @function storeUserData
	 * @param {String} domain - domain to set the driver storage
	 * @returns {Promise}
	 */
	async storeUserData(domain) {
		const userData = await this.getUserData();

		if (userData === null) {
			// Nothing to store
			return Promise.resolve();
		}
		const userDataKey = this.getUserDataStorageKey(domain);
		const storageRecord = {
			userData,
			storedTime: Date.now(),
		};
		backgroundMessaging.setDriverStorage(userDataKey, storageRecord);
		log('Stored user data: %O', storageRecord);
		return Promise.resolve();
	}

	/**
	 * @function restoreUserData
	 * @param {String} domain - domain to access the driver storage
	 * @param {Number} [maxStorageLifetime] - optional, the lifetime of the stored data (default 10 mins)
	 * @returns {Promise}
	 */
	async restoreUserData(domain, maxStorageLifetime = 600000) {
		const userDataKey = this.getUserDataStorageKey(domain);
		const driverStorage = await backgroundMessaging.getDriverStorage(userDataKey);

		if (driverStorage) {
			const { storedTime, userData } = driverStorage;
			// Clear stored user data
			backgroundMessaging.deleteDriverStorage(userDataKey);

			if (Date.now() - storedTime <= maxStorageLifetime) {
				log('Restore user data: %O', userData);
				return this.setUserData(userData);
			}
			log('User data is outdated, skip restoring');
		}
		return Promise.resolve();
	}

	/**
	 * @function getUserData - get user data which should be stored
	 * @returns {Object} - object with required user data which should be restored
	 */
	getUserData() {
		return null;
	}

	/**
	 * @function setUserData - restore user data on page
	 * @param {Object} userData - data which was saved and should be restored
	 * @returns {Promise}
	 */
	setUserData() {
		return Promise.resolve();
	}

	/**
	 * @function getUserDataStorageKey - unique key for storing user data per merchant
	 * @returns {String}
	 */
	getUserDataStorageKey(domain) {
		// Append `_userData` to avoid possible conflicts with created storage from driver
		return `${domain}_userData`;
	}

	/**
	 * Parses start price from page
	 *
	 * @function getStartPrice
	 * @returns {Promise<Number>|Number} - the total from the page
	 */
	getStartPrice() {
		let startPrice = Number.MAX_SAFE_INTEGER;
		const totalElementConfig = this.TOTAL_ELEMENT_CONFIG;
		if (totalElementConfig) {
			const { selector, attribute, regex } = totalElementConfig;
			let totalElement;
			// handle case, when there is array of selectors and we try to take totalElement using these selectors in turn
			// for example dell.com, hertz.com
			if (Array.isArray(selector)) {
				selector.forEach(elemSelector => {
					if (!totalElement) {
						totalElement = document.querySelector(elemSelector);
					}
				});
			} else if (typeof selector === 'string') {
				totalElement = document.querySelector(selector);
			} else {
				handleDriverError(
					new Error(`Total selector type mismatch. Expected string | array, got ${typeof selector}`),
				);
			}

			if (totalElement) {
				let value = attribute ? totalElement.getAttribute(attribute) : totalElement.textContent;

				if (value && regex) {
					// Set pattern to null to receive error in case pattern was not defined
					// otherwise we may silently return undefined or empty string
					const { pattern = null, group = 0 } = regex;
					value = value.match(pattern)[group];
				}
				startPrice = toFloat(value);
			}
		}
		return startPrice;
	}

	/**
	 * Perform any needed actions and setup to start codes testing
	 *
	 * @function beforeTestCodes
	 *
	 * @returns {Promise}
	 */
	beforeTestCodes() {
		return Promise.resolve();
	}

	/**
	 * @typedef {Object} OriginalData
	 * @property {Number} originalPrice the starting price for the cart
	 * @property {Number} originalItemsCount the starting items count of the cart
	 */
	/**
	 * Perform any needed actions and setup before `checkCodes()` will be executed
	 *
	 * @function beforeCheckCodes
	 * @param {Number} [originalPrice] - optional, the starting price for the cart
	 * @param {Number} [originalItemsCount] - optional, the starting items count of the cart
	 * @returns {Promise.<Number>|Promise.<OriginalData>} - optional, a promise which resolves to the original price of the cart
	 * 														or original data which includes originalItemsCount as well
	 */
	beforeCheckCodes() {
		return Promise.resolve();
	}

	/**
	 * Perform any needed actions and setup before `applyCodes()` will be executed
	 *
	 * @function beforeApplyCodes
	 * @param {String[]} codes
	 * @returns {Promise}
	 */
	beforeApplyCodes() {
		return Promise.resolve();
	}

	/**
	 * @function checkCodes
	 *
	 * @param {String[]} codes
	 * @param {Number} [originalPrice] - optional, the starting price for the cart
	 * @param {Number} [originalItemsCount] - optional, the starting items count of the cart
	 * @returns {Promise}
	 */
	checkCodes() {
		return Promise.resolve();
	}

	/**
	 * Takes each code in @param codes and actually applies it to the cart,
	 * typically through a fetch(...) call.
	 *
	 * @function applyCodes
	 *
	 * @param {String[]} codes
	 * @returns {Promise}
	 */
	applyCodes() {
		return Promise.resolve();
	}

	/**
	 * @function stackCodes
	 *
	 * @param {Object} validationInfo - contains all required info passed by `checkCodes()` for codes stacking
	 * @param {String[]} validationInfo.codes
	 * @param {Float} [validationInfo.originalPrice]
	 * @param {Object[]} [validationInfo.discounts] - contains all codes mapped with their discounts
	 * @returns {Promise}
	 */
	stackCodes() {
		return Promise.resolve();
	}

	/**
	 * @function removeCodes
	 *
	 * @param {String[]} codes - passed in case the driver needs to know specific codes to do removal
	 * @returns {Promise.<Number>|Promise.<OriginalData>} - optional, a promise which resolves to the original price of the cart
	 * 														or original data which includes originalItemsCount as well
	 */
	removeCodes() {
		return Promise.resolve();
	}

	/**
	 * Takes codes from merchant page (or request response) which are available and were not applied by user
	 *
	 * @function getMerchantCodes
	 * @returns {String[]} - codes from merhcant page (or request response if possible)
	 */
	getMerchantCodes() {
		return [];
	}

	/**
	 * Takes codes from merchant page (or request) which are available and were applied by user
	 *
	 * @typedef {Object} ExistingCodeInfo
	 * @prop {String[]} codes - list of codes which had been applied, if discoverable
	 * @prop {Number} codeCount - the number of codes that were applied to the cart
	 */
	/**
	 * @function getExistingCodes
	 * @returns {ExistingCodeInfo}
	 */
	getExistingCodes() {
		return {
			codes: [],
			codeCount: 0,
		};
	}

	/**
	 * Parses pre tax and shipping total from page or request's response
	 *
	 * @function getPreTaxShippingTotal
	 * @returns {Promise<Number>|Number} - the total without tax and shipping
	 */
	getPreTaxShippingTotal() {
		return null;
	}

	/**
	 * @function watchForCartUrl - optional function to set up a listener for url changes on SPAs
	 * @emits bus#CartActive - the event (using the string 'cart-active') that indicates to the app that test codes button should be shown
	 * @emits bus#CartInactive - the event (using the string 'cart-inactive') that indicates to the app that test codes button should be hidden
	 */
	watchForCartUrl(time = 500) {
		let previousState = null;
		let pauseInterval = false;

		const interval = setInterval(async () => {
			if (!pauseInterval) {
				let currentState = null;
				try {
					// Pause interval to avoid triggering multiple times `checkCondition`
					// before the response will be received
					pauseInterval = true;
					currentState = await this.checkCondition();
					pauseInterval = false;
				} catch (e) {
					handleDriverError(e, { source: 'checkCondition' });
					clearInterval(interval);
				}
				if (currentState !== previousState) {
					bus.$emit(currentState ? 'cart-active' : 'cart-inactive');
				}
				previousState = currentState;
			}
		}, time);
		// return the interval to stop it in `watchForCartUrl` if needed from outside
		return interval;
	}

	/**
	 * @function checkCondition - optional function to check for conditions on SPAs
	 * @returns {Boolean}
	 */
	checkCondition() {
		return false;
	}

	/**
	 * Perform custom completion action on Continue to Checkout button
	 * Takes best codes in @param codes
	 *
	 * @function completeExperience
	 *
	 * @param {String[]} codes
	 * @returns {Promise}
	 */
	completeExperience() {
		return Promise.resolve();
	}

	/**
	 * @function completeExperiencePromisified - promisified wrapper function to guarantee that Promise will be returned (or exception from Promise)
	 * @returns {Promise}
	 */
	completeExperiencePromisified(...args) {
		return this._promisify(this.completeExperience, args);
	}

	/**
	 * @function beforeCodesTestingPromisified - promisified wrapper function to guarantee that Promise will be returned (or exception from Promise)
	 * @returns {Promise}
	 */
	beforeTestCodesPromisified(...args) {
		return this._promisify(this.beforeTestCodes, args);
	}

	/**
	 * @function getStartPricePromisified - promisified wrapper function to guarantee that Promise will be returned (or exception from Promise)
	 * @returns {Promise}
	 */
	getStartPricePromisified() {
		return this._promisify(this.getStartPrice);
	}

	/**
	 * @function getMerchantCodesPromisified - promisified wrapper function to guarantee that Promise will be returned (or exception from Promise)
	 * @returns {Promise}
	 */
	getMerchantCodesPromisified(...args) {
		return this._promisify(this.getMerchantCodes, args);
	}

	/**
	 * @function getExistingCodesPromisified - promisified wrapper function to guarantee that Promise will be returned (or exception from Promise)
	 * @returns {Promise}
	 */
	getExistingCodesPromisified(...args) {
		return this._promisify(this.getExistingCodes, args);
	}

	/**
	 * @function removeCodesPromisified - promisified wrapper function to guarantee that Promise will be returned (or exception from Promise)
	 * @returns {Promise}
	 */
	removeCodesPromisified(...args) {
		return this._promisify(this.removeCodes, args);
	}

	/**
	 * @function beforeApplyCodesPromisified - promisified wrapper function to guarantee that Promise will be returned (or exception from Promise)
	 * @returns {Promise}
	 */
	beforeApplyCodesPromisified(...args) {
		return this._promisify(this.beforeApplyCodes, args);
	}

	/**
	 * @function applyCodesPromisified - promisified wrapper function to guarantee that Promise will be returned (or exception from Promise)
	 * @returns {Promise}
	 */
	applyCodesPromisified(...args) {
		return this._promisify(this.applyCodes, args);
	}

	/**
	 * @function beforeCheckCodesPromisified - promisified wrapper function to guarantee that Promise will be returned (or exception from Promise)
	 * @returns {Promise}
	 */
	beforeCheckCodesPromisified(...args) {
		return this._promisify(this.beforeCheckCodes, args);
	}

	/**
	 * @function checkCodesPromisified - promisified wrapper function to guarantee that Promise will be returned (or exception from Promise)
	 * @returns {Promise}
	 */
	checkCodesPromisified(...args) {
		return this._promisify(this.checkCodes, args);
	}

	/**
	 * @function stackCodesPromisified - promisified wrapper function to guarantee that Promise will be returned (or exception from Promise)
	 * @returns {Promise}
	 */
	stackCodesPromisified(...args) {
		return this._promisify(this.stackCodes, args);
	}

	/**
	 * @function _promisify - promisifies given method and return it's result as a Promise
	 * @returns {Promise}
	 */
	_promisify(method, args) {
		return Promise.resolve().then(() => method.apply(this, args));
	}
}
