import Promise from 'bluebird';
import BaseDriver from '../base';
import bus from '../../bus';
import { format, log, sendXhr } from '../../../utils';

const { toFloat } = format;

/* Behavior description:
   Site have very nice API and it works very fast
   so API used to get all needed data because this is a simpler way than
   get data from page.
   Site is SPA, so we have dynamical load content
   and we must wait when all content is loaded to get data from page.
   Using API we don't need to wait anything. */

/* We can apply only one code
   We can't apply new code without removing old
   We can't apply invalid code
   We can apply code only on cart page */

export default class HalstonDriver extends BaseDriver {
	constructor() {
		super();

		this.CART_API_URL = '';
		this.USER_ID = null;
		this.lastResponse = null;
	}

	// Base Driver methods
	beforeTestCodes() {
		return this._getCartJson().then(responseJson => {
			this.lastResponse = responseJson;
		});
	}

	getExistingCodes() {
		const codes = [this.lastResponse.couponCode].filter(Boolean).map(el => el.code);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes() {
		return this._removeCode()
			.then(() => this._getCartJson())
			.then(responseJson => this._parseTotal(responseJson));
	}

	beforeCheckCodes(originalPrice) {
		if (!originalPrice) {
			return this._getCartJson().then(responseJson => this._parseTotal(responseJson));
		}

		return Promise.resolve();
	}

	checkCodes(codes, originalPrice) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};

			return this._applyCode(code).then(response => {
				if (response.status === 500) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				return this._getCartJson().then(responseJson => {
					result.finalPrice = this._parseTotal(responseJson);
					result.finalDiscount = originalPrice - result.finalPrice;

					return this._removeCode().then(() => bus.$emit('code-checked', result));
				});
			});
		});
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	// Halston Driver methods
	_manageCode(method, body = false) {
		const options = {
			method,
			credentials: 'include',
		};
		if (body) {
			options.headers = { 'content-type': 'application/json;charset=UTF-8' };
			options.body = body;
		}
		return fetch(`${this.CART_API_URL}/${this.USER_ID}/coupon`, options);
	}

	_removeCode() {
		return this._manageCode('DELETE');
	}

	_applyCode(code) {
		const body = JSON.stringify({ couponCode: code });

		return this._manageCode('PUT', body);
	}

	_getCartJson() {
		return sendXhr(this.CART_API_URL, {
			method: 'GET',
			credentials: 'include',
			headers: {
				accept: 'application/json, text/plain, */*',
			},
		})
			.then(response => response.json())
			.then(json => {
				this.USER_ID = json.id;
				return json;
			});
	}

	_parseTotal(json) {
		return toFloat(json.financial.total / 100);
	}
}
